import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import { withStyles } from "@material-ui/core/styles";

import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Drawer from "@material-ui/core/Drawer";
import Button from "@material-ui/core/Button";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";

import iconSBwhiteMore from "assets/img/iconSBwhiteMore.svg";
import Hidden from "@material-ui/core/Hidden";

import Typography from "@material-ui/core/Typography";

import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";

const styles = {
    headerAppRoot: {
        maxHeight: 64,
        flexGrow: 0
    },
    flex: {
        flex: 1
    },
    flexNo: {
        flex: 0
    },
    logoButton: {
        marginLeft: -12,
        marginRight: 0,
        minWidth: 40,
        paddingLeft: 8,
        paddingRight: 8
    },
    menuButton: {
        marginRight: -12,
        marginLeft: -20
    },
    list: {
        width: 250
    }
};

class HeaderAppBar extends React.Component {
    state = {
        drawerOpen: false
    };
    toggleDrawer = open => () => {
        this.setState({
            drawerOpen: open
        });
    };
    getBrand = () => {
        var name;
        this.props.routes.map((prop, key) => {
            // console.log(
            //     "prop.path" +
            //         prop.path +
            //         " props.location" +
            //         this.props.location.pathname
            // );
            if (prop.path === this.props.location.pathname) {
                name = prop.viewName;
                console.log(">>>>>>>" + name);
            }
            return null;
        });
        if (name === null) {
            name = "";
        }
        return name;
    };

    getRouteList = () => {
        const rlist = this.props.routes.filter(item => item.path !== "/");

        const routelist = rlist.map(item => (
            <ListItem
                component={Link}
                to={item.path}
                button
                key={item.viewName}
            >
                <ListItemText primary={item.viewName} />
            </ListItem>
        ));
        return <List>{routelist}</List>;
    };

    render() {
        const { classes } = this.props;
        const sideList = (
            <div className={classes.list}>{this.getRouteList()}</div>
        );

        return (
            <div className={classes.headerAppRoot}>
                <Drawer
                    anchor="right"
                    open={this.state.drawerOpen}
                    onClose={this.toggleDrawer(false)}
                >
                    <div
                        tabIndex={0}
                        role="button"
                        onClick={this.toggleDrawer(false)}
                        onKeyDown={this.toggleDrawer(false)}
                    >
                        {sideList}
                    </div>
                </Drawer>
                <AppBar position="fixed">
                    <Toolbar>
                        <Button
                            className={classes.logoButton}
                            component={Link}
                            to="/"
                        >
                            <img
                                style={{ width: 40, height: 40 }}
                                src={iconSBwhiteMore}
                                alt="logo"
                            />
                        </Button>
                        <Hidden xsDown>
                            <Typography
                                variant="subheading"
                                color="inherit"
                                className={classes.flexNo}
                            >
                                SANBeaman
                            </Typography>
                        </Hidden>

                        <Typography
                            variant="title"
                            align="center"
                            color="inherit"
                            className={classes.flex}
                        >
                            {this.getBrand()}
                        </Typography>

                        <IconButton
                            onClick={this.toggleDrawer(true)}
                            className={classes.menuButton}
                            color="inherit"
                            aria-label="Menu"
                        >
                            <MenuIcon />
                        </IconButton>
                    </Toolbar>
                </AppBar>
            </div>
        );
    }
}

HeaderAppBar.propTypes = {
    classes: PropTypes.object.isRequired
};

export default withStyles(styles)(HeaderAppBar);



