import React, { Component } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import {
    MuiThemeProvider,
    createMuiTheme,
    withStyles
} from "@material-ui/core/styles";
// import AppBar from "@material-ui/core/AppBar";
// import Toolbar from "@material-ui/core/Toolbar";
// import Paper from "@material-ui/core/Paper";
// import Typography from "@material-ui/core/Typography";
// import appStyle from "assets/theme/jss/matui-sb-react/appStyle.js";
// import { container } from "assets/theme/jss/matui-sb.js";
import theme from "assets/theme/matui-theme_BlueGrey-Lime";
// import Grid from "@material-ui/core/Grid";
import { HeaderAppBar, Footer } from "components";

import contentRoutes from "routes/contentRoutes";
const customtheme = createMuiTheme(theme);
const appStyle = theme => ({
    wrapper: {
        position: "relative",
        // top: "0",
        height: "100vh",
        display: "flex",
        flexFlow: "column nowrap",
        justifyContent: "space-between"
    },
    mainPanel: {
        // [theme.breakpoints.up("md")]: {
        //     width: `calc(100% - ${drawerWidth}px)`
        // },
        // overflow: "auto",
        // position: "relative",
        // float: "right",
        // ...transition,
        paddingTop: 82,
        flexGrow: 1,

        // maxHeight: "calc(100% - 123px)",
        // maxHeight: "100%",
        // minHeight: "calc(100% - 123px)",
        width: "100%",
        overflowScrolling: "touch"
    },
    content: {
        // marginTop: "70px",
        padding: "30px 15px",
        minHeight: "calc(100% - 123px)"
    },
    footer: {
        paddingTop: 0

        // selfAlign: "flex-end",
        // width: "100%",
        // height: 64
        // color: theme.palette.primary.dark,
        // backgroundColor: theme.palette.secondary.main
    }
    // container: {
    //     paddingRight: "15px",
    //     paddingLeft: "15px",
    //     marginRight: "auto",
    //     marginLeft: "auto"
    // },
    // map: {
    //     marginTop: "70px"
    // }
});

const switchRoutes = (
    <Switch>
        {contentRoutes.map((prop, key) => {
            if (prop.redirect)
                return <Redirect from={prop.path} to={prop.to} key={key} />;
            return (
                <Route path={prop.path} component={prop.component} key={key} />
            );
        })}
    </Switch>
);
class Main extends Component {
    state = {
        mobileOpen: false
    };
    handleDrawerToggle = () => {
        this.setState({ mobileOpen: !this.state.mobileOpen });
    };
    render() {
        const { classes, ...rest } = this.props;
        return (
            <MuiThemeProvider theme={customtheme}>
                <div className={classes.wrapper}>
                    <HeaderAppBar
                        routes={contentRoutes}
                        handleDrawerToggle={this.handleDrawerToggle}
                        {...rest}
                    />
                    <div className={classes.mainPanel} ref="mainPanel">
                        {/* <div className={classes.content}> */}
                        {/* <div className={classes.container}> */}
                        {switchRoutes}
                        {/* </div> */}
                    </div>
                    {/* </div> */}

                    {/* <Paper className={classes.footer}>
                        <Toolbar>
                            <Typography variant="title" color="inherit">
                                Title
                            </Typography>
                        </Toolbar>
                    </Paper> */}
                    <div className={classes.footer}>
                        <Footer />
                    </div>
                </div>
            </MuiThemeProvider>
        );
    }
}
export default withStyles(appStyle)(Main);
// export default Help;



