import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Tooltip from "@material-ui/core/Tooltip";
import Paper from "@material-ui/core/Paper";

import tileData from "./data";

const portfolioStyles = theme => ({
    portfolioWrapper: {
        paddingTop: 16
        // height: 800
        // flexGrow: 1
    },
    gridWrapper: {
        padding: "0 16px"
    },
    demo: {
        // height: "100%"
    },
    cardHeader: {
        backgroundColor: theme.palette.primary.light
    },
    cardIcons: {},
    // paper: {
    //     padding: theme.spacing.unit * 2,
    //     height: "100%",
    //     position: "relative"
    // },
    card: {
        color: theme.palette.primary.dark
        // width: 300
    },
    cardContent: {
        ...theme.typography.body1
        // minHeight: 200
    },
    cardDescription: {
        ...theme.typography.caption,
        paddingLeft: 8
    },
    paperFoot: {
        width: "100%",
        marginTop: 16,
        backgroundColor: theme.palette.primary.light
    },
    paperFootText: {
        paddingTop: 8,
        paddingBottom: 8
    }
});

const styles = {
    ulist: {
        paddingLeft: 8,
        marginTop: 0,
        marginBottom: 0,
        marginLeft: 8
    }
};

class Portfolio extends React.Component {
    state = {
        direction: "row",
        justify: "space-around",
        alignItems: "flex-start"
    };
    getDescriptionList = txtarray => {
        const dlist = txtarray.map((item, index) => (
            <li key={index}>{item}</li>
        ));
        return <ul style={styles.ulist}>{dlist}</ul>;
    };
    getSkillList = (iconarray, pid) => {
        const ilist = iconarray.map((item, index) => (
            <Grid item key={index}>
                <Tooltip id={item.name + pid} title={item.name}>
                    {item.svg}
                </Tooltip>
            </Grid>
        ));
        return (
            <Grid
                container
                justify="flex-start"
                alignItems="flex-start"
                wrap="nowrap"
                spacing={8}
            >
                {ilist}
            </Grid>
        );
    };
    render() {
        const { classes } = this.props;
        const { alignItems, direction, justify } = this.state;
        return (
            <Grid container className={classes.portfolioWrapper}>
                <Grid className={classes.gridWrapper} item xs={12}>
                    <Grid
                        container
                        spacing={16}
                        className={classes.demo}
                        alignItems={alignItems}
                        direction={direction}
                        justify={justify}
                    >
                        {tileData.map(tile => (
                            <Grid key={tile.projectID} item xs={12} sm={6}>
                                <Card className={classes.card}>
                                    <CardHeader
                                        className={classes.cardHeader}
                                        title={tile.title}
                                        subheader={tile.client}
                                    />
                                    <CardContent
                                        className={classes.cardContent}
                                    >
                                        {this.getDescriptionList(
                                            tile.description
                                        )}
                                    </CardContent>

                                    <CardContent>
                                        <div className={classes.cardIcons}>
                                            {this.getSkillList(
                                                tile.tools,
                                                tile.projectID
                                            )}
                                        </div>
                                        <div className={classes.cardIcons}>
                                            {this.getSkillList(
                                                tile.skills,
                                                tile.projectID
                                            )}
                                        </div>
                                    </CardContent>
                                </Card>
                            </Grid>
                        ))}
                    </Grid>
                </Grid>
                <Paper
                    className={classes.paperFoot}
                    elevation={0}
                    square={true}
                >
                    <Typography
                        className={classes.paperFootText}
                        variant="caption"
                        align="center"
                    >
                        * Many of the SVG icons on site are from{" "}
                        <a href="http://konpa.github.io/devicon/"> Devicon</a>
                    </Typography>
                </Paper>
            </Grid>
        );
    }
}

Portfolio.propTypes = {
    classes: PropTypes.object.isRequired
};

export default withStyles(portfolioStyles)(Portfolio);



