import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";

import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import { autoPlay } from "react-swipeable-views-utils";

import SwipeableViews from "react-swipeable-views";
import landingViewData from "./landingViewData";
import Typography from "@material-ui/core/Typography";
// import sblogo from "assets/img/sb.svg";

const AutoPlayComponent = autoPlay(SwipeableViews);
const landingStyles = theme => ({
    root: {
        // padding: "0 80px"
        flex: "1 auto",
        alignItems: "center"
    },
    cardContent: {},
    // paper: {
    //     padding: theme.spacing.unit * 4,
    //     textAlign: "center",
    //     fontSize: "2rem",
    //     width: "50%",
    //     margin: "auto",
    //     color: theme.palette.text.secondary
    // },
    slideContainer: {
        padding: "0 10px",
        textAlign: "center",
        fontSize: "2rem",
        width: 200
    },
    slideCard: {
        display: "flex",
        flexFlow: "column nowrap",
        justifyContent: "space-around",

        margin: 15,
        minHeight: 200,
        verticalAlign: "middle",

        backgroundColor: "#efefef"
    },
    slideCardContent: {
        padding: 10
    },
    slideCardContentText: {
        textAlign: "center",
        fontStyle: "italic",
        fontSize: "2rem",
        lineHeight: "2rem",
        color: theme.palette.text.secondary
    },
    txt: {
        textAlign: "left"
    }
});

function Landing(props) {
    const { classes } = props;

    const styles = {
        slideWrapper: {
            padding: "0 10%"
        },
        slideContainer: {
            padding: "50px"
        }
    };
    const listItems = landingViewData.map(item => (
        // Correct! Key should be specified inside the array.
        <Card className={classes.slideCard} key={item.key.toString()}>
            <CardContent className={classes.slideCardContent}>
                <Typography className={classes.slideCardContentText}>
                    {item.txt}
                </Typography>
            </CardContent>
        </Card>
    ));
    return (
        // <div className={classes.root}>
        // <Grid container spacing={8}>
        //     <Grid item xs={12}>

        <AutoPlayComponent
            enableMouseEvents
            style={styles.slideWrapper}
            // slideStyle={styles.slideContainer}
        >
            {listItems}
        </AutoPlayComponent>

        //     </Grid>
        // </Grid>
        // </div>
    );
}

Landing.propTypes = {
    classes: PropTypes.object.isRequired
};

export default withStyles(landingStyles)(Landing);



