import React, { Component } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import {
    MuiThemeProvider,
    createMuiTheme,
    withStyles
} from "@material-ui/core/styles";
// import ContainerDimensions from "react-container-dimensions";

// import AppBar from "@material-ui/core/AppBar";
// import Toolbar from "@material-ui/core/Toolbar";
// import Paper from "@material-ui/core/Paper";
// import Typography from "@material-ui/core/Typography";
// import appStyle from "assets/theme/jss/matui-sb-react/appStyle.js";
// import { container } from "assets/theme/jss/matui-sb.js";
import theme from "assets/theme/matui-theme_BlueGrey-Lime";
// import Grid from "@material-ui/core/Grid";
import { HeaderAppBar, Footer } from "components";

import contentRoutes from "routes/contentRoutes";
const customtheme = createMuiTheme(theme);
const appStyle = theme => ({
    wrapper: {
        // position: "relative",
        // top: "0",
        // height: "100%",
        height: "100vh",
        display: "flex",
        flexFlow: "column nowrap"
        // justifyContent: "center"
    },
    mainHeader: {
        flex: "0 0 64px",
        // display: "flex",
        // flexGrow: 0,
        height: 64,
        // backgroundColor: "#ff0000",
        zIndex: 99
    },
    mainPanel: {
        // [theme.breakpoints.up("md")]: {
        //     width: `calc(100% - ${drawerWidth}px)`
        // },
        // overflow: "auto",
        position: "relative",
        // float: "right",
        // ...transition,
        // paddingTop: 82,
        // display: "flex",
        flex: "1 1 auto",
        overflowY: "auto"
        // minHeight: "calc(100% - 64px)",
        // flexGrow: 1,

        // maxHeight: "calc(100% - 123px)",
        // maxHeight: "100%",
        // minHeight: "calc(100% - 123px)",
        // width: "100%"
        // overflowScrolling: "touch"
    },
    mainFooter: {
        flex: "0 0 64px",
        paddingTop: 0,
        height: 64,
        // flexGrow: 0,
        // flexShrink: 0,
        // backgroundColor: "#ff0000",
        // selfAlign: "flex-end",
        zIndex: 99
    },
    content: {
        // marginTop: "70px",
        padding: "30px 15px",
        minHeight: "calc(100% - 123px)"
    }

    // container: {
    //     paddingRight: "15px",
    //     paddingLeft: "15px",
    //     marginRight: "auto",
    //     marginLeft: "auto"
    // },
    // map: {
    //     marginTop: "70px"
    // }
});

const switchRoutes = (
    <Switch>
        {contentRoutes.map((prop, key) => {
            if (prop.redirect)
                return <Redirect from={prop.path} to={prop.to} key={key} />;
            return (
                <Route path={prop.path} component={prop.component} key={key} />
            );
        })}
    </Switch>
);
class MainBox extends Component {
    state = {
        mobileOpen: false
    };
    handleDrawerToggle = () => {
        this.setState({ mobileOpen: !this.state.mobileOpen });
    };
    render() {
        const { classes, ...rest } = this.props;
        return (
            <MuiThemeProvider theme={customtheme}>
                <div className={classes.wrapper}>
                    <div className={classes.mainHeader}>
                        {/* <Paper>mainHEader</Paper> */}
                        <HeaderAppBar
                            routes={contentRoutes}
                            handleDrawerToggle={this.handleDrawerToggle}
                            {...rest}
                        />
                    </div>
                    <div className={classes.mainPanel}>
                        {/* <div className={classes.content}> */}
                        {/* <div className={classes.container}> */}
                        {switchRoutes}
                        {/* </div> */}
                    </div>
                    {/* </div> */}

                    {/* <Paper className={classes.footer}>
                        <Toolbar>
                            <Typography variant="title" color="inherit">
                                Title
                            </Typography>
                        </Toolbar>
                    </Paper> */}
                    <div className={classes.mainFooter}>
                        <Footer />
                        {/* <Paper>mainFooters</Paper> */}
                    </div>
                </div>
            </MuiThemeProvider>
        );
    }
}
export default withStyles(appStyle)(MainBox);



