import React, { Component } from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { Parallax } from "react-spring";
import Typography from "@material-ui/core/Typography";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";

import sblogo from "assets/img/sbWhite.svg";
import sblogo2 from "assets/img/sb.svg";

const parallaxStyles = theme => ({
    card: {
        minWidth: 275,
        maxWidth: 500,
        marginLeft: 24,
        marginRight: 24
    },
    parallaxLayerStyle: {
        display: "flex",
        alignItems: "center",
        justifyContent: "center"
    },
    greyLayer1: {
        backgroundColor: "#819ca9",
        opacity: 0.5
    },
    primaryLayerLight: {
        backgroundColor: theme.palette.primary.light,
        opacity: 0.5
    },
    primaryLayerMain: {
        backgroundColor: theme.palette.primary.main,
        opacity: 0.5
    },
    primaryLayerDark: {
        backgroundColor: theme.palette.primary.dark,
        opacity: 0.5
    },
    secondaryLayerLight: {
        backgroundColor: theme.palette.secondary.light,
        opacity: 0.5
    },
    secondaryLayerMain: {
        backgroundColor: theme.palette.secondary.main,
        opacity: 0.5
    },
    secondaryLayerDark: {
        backgroundColor: theme.palette.secondary.dark,
        opacity: 0.5
    }
});
class LandingParallaxVert extends Component {
    render() {
        const { classes } = this.props;
        return (
            <Parallax ref={ref => (this.parallax = ref)} pages={6}>
                <Parallax.Layer
                    offset={0}
                    speed={1}
                    className={classes.greyLayer1}
                />
                <Parallax.Layer
                    offset={1}
                    speed={1}
                    className={classes.secondaryLayerDark}
                />
                <Parallax.Layer
                    offset={2}
                    speed={1}
                    factor={1}
                    className={classes.primaryLayerDark}
                />
                <Parallax.Layer
                    offset={3}
                    speed={1}
                    className={classes.primaryLayerDark}
                />
                <Parallax.Layer
                    offset={4}
                    speed={1}
                    factor={1}
                    className={classes.secondaryLayerDark}
                />
                <Parallax.Layer
                    offset={5}
                    speed={1}
                    className={classes.primaryLayerMain}
                />
                <Parallax.Layer
                    offset={0}
                    speed={0}
                    factor={6}
                    className={classes.primaryLayerLight}
                />
                <Parallax.Layer
                    offset={1.3}
                    speed={-0.3}
                    style={{ pointerEvents: "none" }}
                >
                    <img
                        src={sblogo2}
                        alt="sblogo"
                        // src={url("satellite4")}
                        style={{
                            width: "15%",
                            marginLeft: "80%",
                            opacity: 0.8
                        }}
                    />
                </Parallax.Layer>

                <Parallax.Layer
                    offset={3.3}
                    speed={-0.3}
                    style={{ pointerEvents: "none" }}
                >
                    <img
                        src={sblogo2}
                        alt="sblogo"
                        // src={url("satellite4")}
                        style={{
                            width: "15%",
                            marginLeft: "40%",
                            opacity: 0.8
                        }}
                    />
                </Parallax.Layer>
                <Parallax.Layer
                    offset={5.3}
                    speed={-0.3}
                    style={{ pointerEvents: "none" }}
                >
                    <img
                        src={sblogo2}
                        alt="sblogo"
                        // src={url("satellite4")}
                        style={{ width: "15%", marginLeft: "5%", opacity: 0.8 }}
                    />
                </Parallax.Layer>
                <Parallax.Layer
                    offset={0.5}
                    speed={0.8}
                    style={{ opacity: 0.4 }}
                >
                    <img
                        src={sblogo}
                        alt="sblogo"
                        style={{
                            display: "block",
                            width: "5%",
                            marginLeft: "55%"
                        }}
                    />
                    <img
                        src={sblogo}
                        alt="sblogo"
                        style={{
                            display: "block",
                            width: "10%",
                            marginLeft: "15%"
                        }}
                    />
                </Parallax.Layer>

                <Parallax.Layer
                    offset={1.5}
                    speed={0.5}
                    style={{ opacity: 0.4 }}
                >
                    <img
                        src={sblogo}
                        alt="sblogo"
                        style={{
                            display: "block",
                            width: "10%",
                            marginLeft: "70%"
                        }}
                    />
                </Parallax.Layer>

                <Parallax.Layer
                    offset={3.5}
                    speed={0.2}
                    style={{ opacity: 0.4 }}
                >
                    <img
                        src={sblogo}
                        alt="sblogo"
                        style={{
                            display: "block",
                            width: "10%",
                            marginLeft: "10%"
                        }}
                    />
                </Parallax.Layer>

                <Parallax.Layer
                    offset={4.5}
                    speed={-0.1}
                    style={{ opacity: 0.4 }}
                >
                    <img
                        src={sblogo}
                        alt="sblogo"
                        style={{
                            display: "block",
                            width: "10%",
                            marginLeft: "60%"
                        }}
                    />
                    <img
                        src={sblogo}
                        alt="sblogo"
                        style={{
                            display: "block",
                            width: "15%",
                            marginLeft: "30%"
                        }}
                    />
                    <img
                        src={sblogo}
                        alt="sblogo"
                        style={{
                            display: "block",
                            width: "10%",
                            marginLeft: "80%"
                        }}
                    />
                </Parallax.Layer>

                <Parallax.Layer
                    offset={5.6}
                    speed={0.4}
                    style={{ opacity: 0.6 }}
                >
                    <img
                        src={sblogo}
                        alt="sblogo"
                        style={{
                            display: "block",
                            width: "15%",
                            marginLeft: "75%"
                        }}
                    />
                </Parallax.Layer>
                <Parallax.Layer
                    offset={0}
                    speed={0.1}
                    onClick={() => this.parallax.scrollTo(1)}
                    className={classes.parallaxLayerStyle}
                >
                    <Card className={classes.card}>
                        <CardContent>
                            <Typography variant="headline">
                                Provide the small to medium size business and
                                organizations, with an outsourced Chief
                                Technology Officer, Research and Development
                                Department, and/or IT department.
                            </Typography>
                        </CardContent>
                    </Card>
                </Parallax.Layer>

                <Parallax.Layer
                    offset={1}
                    speed={0.1}
                    onClick={() => this.parallax.scrollTo(2)}
                    className={classes.parallaxLayerStyle}
                >
                    <Card className={classes.card}>
                        <CardContent>
                            <Typography variant="headline">
                                Develop comprehensive strategic technology plans
                                for company-wide initiatives, mobile and web
                                applications, or client projects basis.
                            </Typography>
                        </CardContent>
                    </Card>
                </Parallax.Layer>

                <Parallax.Layer
                    offset={2}
                    speed={0.1}
                    onClick={() => this.parallax.scrollTo(3)}
                    className={classes.parallaxLayerStyle}
                >
                    <Card className={classes.card}>
                        <CardContent>
                            <Typography variant="headline">
                                Collaborate and document the best way(s) to
                                complete the project given the creative
                                direction and technical requirements.
                            </Typography>
                        </CardContent>
                    </Card>
                </Parallax.Layer>
                <Parallax.Layer
                    offset={3}
                    speed={0.1}
                    onClick={() => this.parallax.scrollTo(4)}
                    className={classes.parallaxLayerStyle}
                >
                    <Card className={classes.card}>
                        <CardContent>
                            <Typography variant="headline">
                                Manage and lead both large and small teams
                                located in a single office or spread across
                                multiple locations and time zones
                            </Typography>
                        </CardContent>
                    </Card>
                </Parallax.Layer>

                <Parallax.Layer
                    offset={4}
                    speed={0.1}
                    onClick={() => this.parallax.scrollTo(5)}
                    className={classes.parallaxLayerStyle}
                >
                    <Card className={classes.card}>
                        <CardContent>
                            <Typography variant="headline">
                                Streamline the development process as well as
                                manage other developers or dig in and do actual
                                development when necessary.
                            </Typography>
                        </CardContent>
                    </Card>
                </Parallax.Layer>
                <Parallax.Layer
                    offset={5}
                    speed={-0}
                    className={classes.parallaxLayerStyle}
                    onClick={() => this.parallax.scrollTo(0)}
                >
                    <Card className={classes.card}>
                        <CardContent>
                            <Typography variant="headline">
                                Keep abreast of the latest technologies, such as
                                high-level innovations like Augmented / Virtual
                                Reality to the best emerging developer tools and
                                technologies.
                            </Typography>
                        </CardContent>
                    </Card>
                </Parallax.Layer>
            </Parallax>
        );
    }
}
LandingParallaxVert.propTypes = {
    classes: PropTypes.object.isRequired
};

export default withStyles(parallaxStyles)(LandingParallaxVert);



