import { LandingParallaxVert, Portfolio } from "views";

const contentRoutes = [
    {
        path: `${process.env.PUBLIC_URL}/portfolio`,
        viewName: "Portfolio",
        component: Portfolio
    },
    { path: "/", viewName: "", component: LandingParallaxVert }
];

export default contentRoutes;



